export default {
  data () {
    return {
      menu: [{
        link: 'course',
        value: 'course',
        name: '找课程',
        icon: 'icon-fs-course'
      }, {
        link: 'tech',
        value: 'tech',
        name: '前沿技术',
        icon: 'icon-kexuejishu-'
      }, {
        link: 'my',
        value: 'my',
        name: '我的',
        icon: 'icon-wode'
      }]
    }
  },
  computed: {
    // 当前tab
    curMenu () {
      return this.$route.name
    },
    infoObj () {
      return this.$store.state.user.infoObj
    }
  },
  methods: {
    changeMenu (link) {
      console.log('link', link)
      if (link === 'my') {
        if (this.infoObj === null) {
          // 从来没有创建过信息
          this.$router.push({ path: './selRegType' })
          return
        }
      }
      this.$router.push({ path: './' + link })
    }
  }
}



